* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  padding: 20px;
}
.list {
  display: flex;
  flex-wrap: wrap;
  padding: 15px;
  gap: 15px;
}

button {
  padding: 10px;
  border-radius: 10px;
  margin: 5px;
  cursor: pointer;
  background-color: aqua;
}

.search {
  padding: 10px;
  border-radius: 10px;
  min-width: 300px;
  margin: 5px;
}
